import { VStack } from '@chakra-ui/react';
import useFileListContents from './useFileListContents';

interface Props {
  videos: FileSystemFileHandle[];
}

const MessageVideos: React.FC<Props> = ({ videos }) => {
  const urls = useFileListContents(videos);

  const style = {
    maxW: '32ch',
  };

  return (
    <VStack sx={style}>
      {urls.map((url) => (
        // eslint-disable-next-line jsx-a11y/media-has-caption
        <video key={url} className="bubble" src={url} controls />
      ))}
    </VStack>
  );
};
export default MessageVideos;
