import { Message } from '../MessageType';
import getConversationSlices, {
  ConversationSlice,
} from './getConversationSlices';

export type Metadata = Omit<ConversationSlice, 'messages'>;

export interface TransformedMessage
  extends Omit<Message, 'photos' | 'videos' | 'audio_files' | 'gifs'> {
  photos?: FileSystemFileHandle[];
  videos?: FileSystemFileHandle[];
  audio?: FileSystemFileHandle[];
  gifs?: FileSystemFileHandle[];
}

const paginate = <T>(items: T[], pageSize: number) => {
  const pages = [];

  for (let i = 0; i < items.length; i += pageSize) {
    pages.push(items.slice(i, i + pageSize));
  }

  return pages;
};

const getDirHandle = (
  parentHandle: FileSystemDirectoryHandle,
  dirName: string
) =>
  parentHandle.getDirectoryHandle(dirName, {
    create: true,
  });

const getFileHandlesForMedia = (
  media: { uri: string }[],
  dirHandle: FileSystemDirectoryHandle
) =>
  Promise.all(
    media
      .map(({ uri }) => uri.match(/[^/]+\.\w+/)?.[0])
      .filter((filename) => !!filename)
      .map(async (filename) =>
        dirHandle.getFileHandle(filename!, {
          create: true,
        })
      )
  );

const getConversation = async (dirHandle: FileSystemDirectoryHandle) => {
  const slices = await getConversationSlices(dirHandle);

  const metadata = (() => {
    const { messages, ...rest } = slices[0];
    return rest;
  })();

  const allMessages = slices
    .reduce<Message[]>((arr, slice) => arr.concat(slice.messages), [])
    .reverse();

  const transformedMessages: TransformedMessage[] = await Promise.all(
    allMessages.map(async (message) => {
      const {
        photos,
        videos,
        audio_files: audio,
        gifs,
        ...restOfMessage
      } = message;

      const transformedMessage: Partial<TransformedMessage> = {
        ...restOfMessage,
      };

      if (photos)
        transformedMessage.photos = await getFileHandlesForMedia(
          photos ?? [],
          await getDirHandle(dirHandle, 'photos')
        );

      if (videos)
        transformedMessage.videos = await getFileHandlesForMedia(
          videos ?? [],
          await getDirHandle(dirHandle, 'videos')
        );

      if (audio)
        transformedMessage.audio = await getFileHandlesForMedia(
          audio ?? [],
          await getDirHandle(dirHandle, 'audio')
        );

      if (gifs)
        transformedMessage.gifs = await getFileHandlesForMedia(
          gifs ?? [],
          await getDirHandle(dirHandle, 'gifs')
        );

      return transformedMessage as TransformedMessage;
    })
  );

  const pages = paginate(transformedMessages, 800);

  return {
    metadata,
    pages,
  };
};

export default getConversation;
