import { Flex, Heading, useColorModeValue } from '@chakra-ui/react';
import DirectoryPicker from '../directory-api/DirectoryPicker';

interface Props {
  onDirectoryPick: React.ComponentProps<typeof DirectoryPicker>['onPick'];
  title: string;
}

const AppHeader: React.FC<Props> = ({ onDirectoryPick, title }) => {
  const style = {
    backgroundColor: useColorModeValue('gray.50', 'gray.900'),
    justifyContent: 'center',
    alignItems: 'center',
    padding: 2,
    gap: 6,
  };

  return (
    <Flex sx={style}>
      <Heading size="sm">{title}</Heading>
      <DirectoryPicker onPick={onDirectoryPick} />
    </Flex>
  );
};
export default AppHeader;
