import { Button } from '@chakra-ui/react';

type Props = {
  onPick?: (dirHandle: FileSystemDirectoryHandle) => any;
};

const DirectoryPicker: React.FC<Props> = ({ onPick = () => {} }) => {
  const clickHandler = async () => {
    const directoryHandle = await showDirectoryPicker();
    onPick(directoryHandle);
  };

  return (
    <Button type="button" aria-label="Pick directory" onClick={clickHandler}>
      Pick a directory
    </Button>
  );
};
export default DirectoryPicker;
