import { useState, useEffect } from 'react';

const useFileListContents = (files: FileSystemFileHandle[]) => {
  const [urls, setUrls] = useState<string[]>([]);

  useEffect(() => {
    let newUrls: string[] = [];
    let ignore = false;

    (async () => {
      const blobs = await Promise.all(
        files.map(async (file) => file.getFile())
      );
      newUrls = blobs.map((blob) => URL.createObjectURL(blob));
      if (!ignore) setUrls(newUrls);
    })();

    return () => {
      ignore = true;
      newUrls.forEach((url) => URL.revokeObjectURL(url));
    };
  }, [files]);

  return urls;
};

export default useFileListContents;
