import { Flex } from '@chakra-ui/react';
import { TransformedMessage } from '../loaders/getConversation';
import toFancyMessages from '../toFancyMessages';
import MessagesGroup from './MessagesGroup';

type Props = {
  messages: TransformedMessage[];
  ownerName: string;
};

const MessagesList: React.FC<Props> = ({ messages, ownerName }) => {
  console.log(messages);

  const fancyMessages = toFancyMessages(messages);

  const style = {
    flexDirection: 'column',
    gap: 4,
    width: '100%',
    maxWidth: '420px',
    padding: 4,
    margin: '0 auto',
    maxHeight: '100%',
    overflowY: 'auto',
    overscrollBehavior: 'none',
    background: 'black',
  };

  return (
    <Flex sx={style}>
      {fancyMessages.map((group) => (
        <MessagesGroup key={group.id} group={group} userId={ownerName} />
      ))}
    </Flex>
  );
};
export default MessagesList;
