import { Button, Flex, Text, useColorModeValue } from '@chakra-ui/react';
import mathInRange from '../utils/mathInRange';

interface Props {
  size: number;
  current: number;
  onChange: (page: number) => any;
}

const PAGES_MAX_DISTANCE = 3;

const Pagination: React.FC<Props> = ({ size, current, onChange }) => {
  const range = Array.from(
    { length: Math.min(size, PAGES_MAX_DISTANCE * 2 + 1) },
    (_v, i) =>
      mathInRange(
        0,
        current - PAGES_MAX_DISTANCE,
        size - PAGES_MAX_DISTANCE * 2 - 1
      ) + i
  );

  const style = {
    backgroundColor: useColorModeValue('gray.50', 'gray.900'),
    padding: 2,
    justifyContent: 'center',
    gap: 2,
  };

  return (
    <Flex sx={style}>
      {current > PAGES_MAX_DISTANCE && (
        <>
          <Button size="sm" onClick={() => onChange(0)}>
            1
          </Button>
          <Text fontSize="20px">...</Text>
        </>
      )}
      {range.map((index) => (
        <Button
          size="sm"
          colorScheme={index === current ? 'messenger' : 'gray'}
          onClick={() => onChange(index)}
        >
          {index + 1}
        </Button>
      ))}
      {current < size - PAGES_MAX_DISTANCE - 1 && (
        <>
          <Text fontSize="20px">...</Text>
          <Button size="sm" onClick={() => onChange(size - 1)}>
            {size}
          </Button>
        </>
      )}
    </Flex>
  );
};
export default Pagination;
