import arrayFromAsyncIterator from '../../utils/arrayFromAsyncIterator';
import { Message } from '../MessageType';
import { isMessagesFile, sortByIndex } from './filters';
import getParsedJSONFile from './getParsedJSONFile';

export type ConversationSlice = {
  title: string;
  is_still_participant: boolean;
  thread_type: 'Regular' | 'RegularGroup';
  thread_path: string;
  participants: {
    name: string;
  }[];
  messages: Message[];
};

const getConversationSlices = async (
  dirHandle: FileSystemDirectoryHandle
): Promise<ConversationSlice[]> => {
  const files = await arrayFromAsyncIterator(dirHandle.values());
  const messagesJSONFiles = files
    .filter(isMessagesFile)
    .sort(sortByIndex) as FileSystemFileHandle[];

  if (!messagesJSONFiles) return [];

  const conversationSlices = (
    await Promise.all(messagesJSONFiles.map(getParsedJSONFile))
  ).filter((obj) => obj !== null);

  return conversationSlices;
};

export default getConversationSlices;
