import { useState } from 'react';
import DirectoryContext from './directory-api/DirectoryContext';
import AppHeader from './layout/AppHeader';
import Pagination from './layout/Pagination';
import RootLayout from './layout/RootLayout';
import MessagesList from './messages/components/MessagesList';
import getConversation, {
  Metadata,
  TransformedMessage,
} from './messages/loaders/getConversation';

const App = () => {
  const [directoryHandle, setDirectoryHandle] =
    useState<FileSystemDirectoryHandle>();
  const [conversation, setConversation] = useState<{
    metadata: Metadata;
    pages: TransformedMessage[][];
  }>();
  const [page, setPage] = useState<number>(0);

  const pickHandler = async (newHandle: FileSystemDirectoryHandle) => {
    setDirectoryHandle(newHandle);
    const pickedConversation = await getConversation(newHandle);
    setConversation(pickedConversation);
  };

  return (
    <RootLayout>
      <DirectoryContext.Provider value={directoryHandle}>
        <AppHeader
          onDirectoryPick={pickHandler}
          title={conversation?.metadata.title ?? ''}
        />
        {conversation && (
          <MessagesList
            key={conversation.metadata.thread_path + page}
            messages={conversation.pages[page]}
            ownerName={conversation.metadata.participants[1].name}
          />
        )}
        {conversation && (
          <Pagination
            size={conversation.pages.length}
            current={page}
            onChange={setPage}
          />
        )}
      </DirectoryContext.Provider>
    </RootLayout>
  );
};

export default App;
