import { Image, VStack } from '@chakra-ui/react';
import useFileListContents from './useFileListContents';

interface Props {
  photos: FileSystemFileHandle[];
}

const MessagePhotos: React.FC<Props> = ({ photos }) => {
  const urls = useFileListContents(photos);

  const style = {
    maxW: '32ch',
  };

  return (
    <VStack sx={style}>
      {urls.map((url) => (
        <Image key={url} className="bubble" src={url} />
      ))}
    </VStack>
  );
};
export default MessagePhotos;
