import { TransformedMessage } from './loaders/getConversation';

export interface FancyMessagesGroup {
  date?: number;
  id: string | number;
  sender: string;
  messages: TransformedMessage[];
}

const bigTimeGap = 60e3 * 15;
const moderateTimeGap = 60e3 * 2;

let i = 0;

const toFancyMessages = (messages: TransformedMessage[]) => {
  const fancyMessages: FancyMessagesGroup[] = [];

  const appendAsClose = (message: TransformedMessage) => {
    fancyMessages[0].messages.push(message);
  };

  const appendAsModerate = (message: TransformedMessage) => {
    fancyMessages.unshift({
      id: i,
      sender: message.sender_name,
      messages: [message],
    });
  };

  const appendAsSeperate = (message: TransformedMessage) => {
    fancyMessages.unshift({
      id: i,
      date: message.timestamp_ms,
      sender: message.sender_name,
      messages: [message],
    });
  };

  let prevDate = -Infinity;
  let prevSender = '';

  (messages as FancyMessagesGroup['messages']).reverse().forEach((message) => {
    const date = message.timestamp_ms;
    const isSenderTheSame = prevSender === message.sender_name;
    const isCloseInTime = date - prevDate < moderateTimeGap;
    const isModerateInTime = date - prevDate < bigTimeGap;

    if (!isModerateInTime) appendAsSeperate(message);
    else if (!isSenderTheSame) appendAsModerate(message);
    else if (isCloseInTime) appendAsClose(message);
    else appendAsModerate(message);

    i += 1;
    prevDate = date;
    prevSender = message.sender_name;
  });

  return fancyMessages.reverse();
};

export default toFancyMessages;
