const messageFileRegEx = /message_(\d+)\.json/;

export const isMessagesFile = ({ name, kind }: FileSystemHandle) =>
  kind === 'file' && name.match(messageFileRegEx);

export const sortByIndex = (a: FileSystemHandle, b: FileSystemHandle) =>
  +a.name.match(messageFileRegEx)![1] - +b.name.match(messageFileRegEx)![1];
