import { Flex } from '@chakra-ui/react';
import React from 'react';
import InteractiveContent from './InteractiveContent';
import MessageBubble from './MessageBubble';
import MessageLink from './MessageLink';
import getDayjsInstance from '../../utils/getDayjsInstance';
import formatDateInformative from '../formatDateInformative';
import { TransformedMessage } from '../loaders/getConversation';
import MessagePhotos from './MessagePhotos';
import MessageVideos from './MessageVideos';

const dayjs = getDayjsInstance();

interface Props {
  isDirectionTo: boolean;
  data: TransformedMessage;
}

const Message: React.FC<Props> = ({ isDirectionTo, data }) => {
  const dateString = formatDateInformative(dayjs(data.timestamp_ms));
  const { content, is_unsent: isDeleted } = data;

  const containerStyle = {
    '&:hover .toolbar': {
      opacity: 1,
    },
    flexDirection: isDirectionTo ? 'row' : 'row-reverse',
  };

  return (
    <Flex sx={containerStyle} className="message" gap={2}>
      {(!!content || isDeleted) && (
        <MessageBubble
          colored={isDirectionTo}
          isDeleted={isDeleted}
          dateString={dateString}
          isDirectionTo={isDirectionTo}
        >
          <InteractiveContent
            textContent={content ?? ''}
            linkComponent={MessageLink}
          />
        </MessageBubble>
      )}
      {!!data.photos && <MessagePhotos photos={data.photos ?? []} />}
      {!!data.videos && <MessageVideos videos={data.videos ?? []} />}
    </Flex>
  );
};
export default React.memo(Message);
