const textDecoder = new TextDecoder('UTF-8');

const fixEncoding = (str: string) => {
  const regex =
    /\\u00([a-f\d]{2})\\u00([a-f\d]{2})\\u00([a-f\d]{2})\\u00([a-f\d]{2})|\\u00([a-f\d]{2})\\u00([a-f\d]{2})/g;
  return str.replace(regex, (_, g1, g2, g3, g4, g5, g6) => {
    if (g5) {
      return textDecoder.decode(
        new Uint8Array([parseInt(g5, 16), parseInt(g6, 16)])
      );
    }
    return textDecoder.decode(
      new Uint8Array([
        parseInt(g1, 16),
        parseInt(g2, 16),
        parseInt(g3, 16),
        parseInt(g4, 16),
      ])
    );
  });
};

export default fixEncoding;
