import fixEncoding from './fixEncoding';

const getParsedJSONFile = async (fileHandle: FileSystemFileHandle) => {
  try {
    const file = await fileHandle.getFile();
    const textContent = await file.text();
    const fixedContent = fixEncoding(textContent);
    return JSON.parse(fixedContent);
  } catch {
    return null;
  }
};

export default getParsedJSONFile;
